import { Component, OnInit } from '@angular/core';

import { IUser, ITeam, IProject } from '../../interface';

import { HomeService } from '../service/home.service';
import { Router } from '@angular/router';
import { UserService } from '../../users/user-service/user.service';


declare var swal: any;


@Component({
  selector: 'app-side-bar',
  templateUrl: './side-bar.component.html',
  styleUrls: ['./side-bar.component.css']
})
export class SideBarComponent implements OnInit {

  private user;
  private ready: boolean;


  constructor(
    private _home: HomeService,
    private _user: UserService,
    private _router: Router
  ) { }

  ngOnInit() {
    this.ready = false;

    this._home.get4id().subscribe(_ids => {
      this.user = {};
      this.user.id = _ids.user;
      this.user.uid = _ids.profile;
      this.user.tid = _ids.team;
      this.user.pid = _ids.project;
      this._user.getRealUser(_ids.user).subscribe(_data => {
        this.user.last = _data.last_name;
        this.user.first = _data.first_name;
        this.ready = true;
      })
    }, err => {
      swal({
        title: "Not Logined",
        text: "Please Login",
        type: "error"
      });
      this._router.navigate(['/login'])
    })
  }
}



